import React from 'react'
import Helmet from 'react-helmet'
import FooterInfo from '../partials/FooterInfo'
import ContactForm from '../partials/ContactForm'

const BecomePartnerPage = () => (
  <div>
    <Helmet
      title="Become a Partner - Fitato"
      meta={[
        { name: 'description', content: 'Become a partner.' },
      ]}
    />
    <div className="become-partner-page">

      {/* Hero section may be added */}
    
      <div className="become-partner-section-what">
        <div className="container">
          <h2>What We Do at Fitato</h2>
          <p>At Fitato, we are trying to make fitness fun &amp; flexible. A single pass gives a person access to over 250+ partner Gyms and Fitness Studios suiting the needs of everyone. This means that a person can workout in their neighbourhood gym one day, go for a swim near office the next day, try out Yoga the third day, throw some punches at a Kick-Boxing class the next and so on. Fitness will never be boring with Fitato.</p>
          <p>links</p>
        </div>
      </div>

      {/* Like checkboxes; instead use icons */}
      <div className="become-partner-section-why">
        <div className="container">
          <h2>Why Work With Us</h2>
          <div className="become-partner-section-why__questions">
            <div>Ever faced problem with marketing your studio?</div>
            <div>Is the renewal rate of your studio falling short with passing time?</div>
            <div>Made numerous calls and messages to your lapsed users but have minimal returns?</div>
          </div>
          <p>Well you're not the only one facing these problems.</p>
          <p>We are already working with numerous big and small brands throughout the Pune City and helping them tackle these problems, one step at a time.</p>
        </div>
      </div>

      <div className="become-partner-section-growth">
        <div className="container">
          <h2>Is Fitato Growing? Yes.</h2>
          <p>Fitato has been growing exponentially, specially in the year 2017 and here are some figures reflecting our growth in the last year:</p>
          
        </div>
      </div>

    </div>
    <FooterInfo />
  </div>
)

export default BecomePartnerPage



